import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { OrderRoutingModule } from './order-routing.module';
import { OrderMangerComponent } from './order-manger/order-manger.component';
import { ViewOrderComponent } from './view-order/view-order.component';
import {ShareModule} from '../../share/share.module';
import { ModalEditOrderComponent } from './modal-edit-order/modal-edit-order.component';

@NgModule({
  imports: [
    CommonModule,
    OrderRoutingModule,
    ShareModule
  ],
  declarations: [OrderMangerComponent, ViewOrderComponent, ModalEditOrderComponent],
  entryComponents:[ModalEditOrderComponent]
})
export class OrderModule { }
