import { Component, OnInit } from '@angular/core';
import {FormBuilder, Validators} from '@angular/forms';
import {NzMessageService, NzModalRef, NzModalService} from 'ng-zorro-antd';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {ResetHttpService} from '../../../share/util/reset-http.service';

@Component({
  selector: 'app-modal-edit-order',
  templateUrl: './modal-edit-order.component.html',
  styleUrls: ['./modal-edit-order.component.scss']
})
export class ModalEditOrderComponent implements OnInit {
  serverId;
  forms;
  constructor(
    private fb:FormBuilder,
    private nzmref:NzModalRef,
    private msg:NzMessageService,
    private url:UrlServeService,
    private util:UtilServeService,
    private http:ResetHttpService,
    private modal:NzModalService
  ) {
    this.forms = this.fb.group({
      serviceP:['',[Validators.required]]
    })
  }

  ngOnInit() {
    this.serverId = this.nzmref.getContentComponent();
    console.log(this.serverId);
  }
  commit(){
    for(let control in this.forms.controls){
      this.forms.controls[control].markAsDirty();
      this.forms.controls[control].updateValueAndValidity();
    }

    if(!this.forms.valid){
      return
    }
    var modal = this.modal.confirm({
      nzTitle: '提示',
      nzContent: '调整服务商后，原先的服务商将不能对订单进行任何操作！<br>此订单将由调整后的服务商进行发货处理，请谨慎操作！',
      nzBodyStyle:{color:'#ff0000'},
      nzOnOk: () => new Promise((resolve, reject) => {
        // this.http.get(this.url.updateServe,{
        //     id:'',
        //     merchant_id:this.forms.controls['serviceP'].value
        // },data=>{
        //   console.log(data);
        //   if(data.code == '001000'){
                modal.destroy();
                this.nzmref.destroy();
        //   }else{
        //     this.msg.error(data.message);
        //   }
        // },err=>{
        //   console.log(err);
        // })
      }).catch(() => console.log('Oops errors!'))
    });

  }
  cancle(){
    history.back();
    this.nzmref.destroy()
  }

}
