import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {UrlServeService} from '../../../share/util/url-serve.service';
import {NzMessageService, NzModalService} from 'ng-zorro-antd';
import {UtilServeService} from '../../../share/util/util-serve.service';
import {FormBuilder} from '@angular/forms';
import {ResetHttpService} from '../../../share/util/reset-http.service';
import {ModalEditOrderComponent} from '../modal-edit-order/modal-edit-order.component';

@Component({
  selector: 'app-order-manger',
  templateUrl: './order-manger.component.html',
  styleUrls: ['./order-manger.component.scss']
})
export class OrderMangerComponent implements OnInit {
  validateForm;
  i = 1;
  isEnablemodal;
  isloading = true;
  dataSet = [
    {id:1,name:'1',telephone:1,integral:1,recomm_code:1,region_name:1,status:'1'},
    {id:1,name:'0',telephone:1,integral:1,recomm_code:1,region_name:1,status:'6'},
    {id:1,name:'0',telephone:1,integral:1,recomm_code:1,region_name:1,status:'3'},
    {id:1,name:'1',telephone:1,integral:1,recomm_code:1,region_name:1,status:'2'}
  ];
  allId:any = [];
  constructor(private fb:FormBuilder,
              private msg:NzMessageService,
              private modalService:NzModalService,private router:Router,private url :UrlServeService,private http:ResetHttpService,private util:UtilServeService) {
    this.validateForm = this.fb.group({
      serveArea:[],
      orderType:[],
      phone:[],

    })
  }
  //获取组件
  get serveArea(){
    return this.validateForm.controls.serveArea;
  }
  get orderType(){
    return this.validateForm.controls.orderType;
  }
  get phone(){
    return this.validateForm.controls.phone;
  }

  ngOnInit() {
    this.getDetailList();
    this.isloading = false;
  }
  //页面跳转
  goPager(option){
    this.util.goPage(option);
  }
  //搜索
  searchMore(){
    console.log(this.orderType.value,this.phone.value,this.serveArea.value);
    // this.getDetailList();

  }
  //获取订单列表
  getDetailList(order_type = '',region_id = '',telephone = ''){
    this.isloading =true;
    this.http.get(this.url.orderList,{
      order_type:order_type,
      region_id:region_id,
      telephone:telephone
    },data=>{
      // console.log(data);
      this.dataSet = data.response;
      this.isloading = false;
      // this.dataSet.forEach((v,k)=>{
      //   this.allId.push(v.id);
      // });
    },err=>{
      console.log(err);
    })
  }

  /*
  * 表格操作
  * */

  //创建模态窗 调整服务商
  createModal(data): void {
    this.validateForm.reset();
    var modal = this.modalService.create({
      nzTitle: '调整服务商',
      nzContent: ModalEditOrderComponent,
      nzClosable: false,
      nzComponentParams:data,
      nzFooter:null
    });
    modal.afterOpen.subscribe(() => console.log('[afterOpen] emitted!'));

    // Return a result when closed
    modal.afterClose.subscribe((result) => console.log('[afterClose] The result is:', result));

  }
}
